        //歌词设置播放时间
        formattingGeci:function(str){
          str = str.replace(/(^\s*)|(\s*$)/g, "");
          let strtime =str.substring(1,6); //时间 分秒
          //处理歌词
          //console.log(strtime.split(':'));
          let arrTime = strtime.split(':');
          let fen = parseInt(arrTime[0]) * 60;
          let miao = parseInt(arrTime[1]);
          let allTime = fen + miao;
          //console.log(allTime);
          let strGeci = str.substring(7,str.length); //歌词
          return {
            time: allTime,
            geci: strGeci
          }
        },
        //设置歌词
        setGeci:function(){
          if( typeof this.geci != "string" ){
            this.geci = this.geci.toString();
          }
          //console.log(this.geci);
          this.geci = this.geci.split(',');
          for( let i=0;i<this.geci.length;i++ ){
            let timeJson = this.formattingGeci(this.geci[i]);
            this.geciJson.push(
              {
                time:timeJson.time - 2,
                geci:timeJson.geci
              }
            )
          }
          this.muiscTitle = this.geciJson['0'].geci;
        },
        //设置总时间 精确时分秒
        setTime:function (allTime,toMiao) {
          let timute = parseInt(allTime/60);
          //秒
          let second = allTime%60;
          let seconds;
          if( second <10 ){
            seconds = "0" + second;
          }else {
            seconds = second;
          }
          //console.log(timute + ":" + seconds);
          return  (timute + ":" + seconds);
        },
        //歌词滚动
        geciShow:function (index) {
          //console.log(index)
          //console.log(index);
          //console.log($("#geci p").length - 1);
          this.titileParent = $("#geci").find("div").eq(0).text();
          let ontGeci = $("#geci").find("div").eq(index);
          // console.log(ontGeci.position());
          //console.log(ontGeci);
          if( ontGeci.position() != undefined ){
            let geciTop = ontGeci.position().top;
            let ontGeciTime = parseInt(ontGeci.attr('data-time'));
            let newxtGeci = ontGeci.next();
            let newxtGeciTime = parseInt(newxtGeci.attr('data-time'));

            if( parseInt($("#audio")[0].currentTime) >= ontGeciTime && parseInt($("#audio")[0].currentTime) <= newxtGeciTime  ){
              $("#geci").css("transform","translateY(-"+ geciTop +"px)");
              ontGeci.addClass('active');
            }else if(index == $("#geci div").length - 1){
              $("#geci").find("div").eq($("#geci p").length - 1).addClass('active');
            }else {
              this.geciShowIndex++;
              $("#geci").find("div").eq(this.geciShowIndex).prev().removeClass('active');
            }
          }
        },
        //隐藏播放器
        playerShow:function () {
            this.musicPlayer = !this.musicPlayer;
        }
      },
      //组件销毁前
      beforeDestroy() {
        if(this.timer) { //如果定时器还在运行 或者直接关闭,不用判断
          clearInterval(this.timer);
        }
      },
      //组件装载后 页面显示
      mounted: function () {
        //$("#audio")[0].pause()
  /*      this.titileParent = (this.musicGeci[0].name) + (this.musicGeci[0].singerName);
        this.musicName = this.musicGeci[0].name;
        this.singerName = this.musicGeci[0].singerName;*/
      }
    };

</script>

