<template>
    <div>
      <footer>
        <ul class="tabList">
          <li @click="getIndex(1)">
            <router-link to="/index" >
              <i v-bind:class="index === 1? 'icon-homepage_fill' : 'icon-homepage' " class="iconfont"></i>
              首页
            </router-link>
          </li>
          <li @click="getIndex(2)">
           <router-link to="/MusicListParent" >
             <i v-bind:class="index === 2? 'icon-browse_fill' : 'icon-browse' " class="iconfont"></i>
             发现
           </router-link>
          </li>
          <li @click="getIndex(3)">
            <router-link to="/diary">
              <i v-bind:class="index === 3? 'icon-select_fill' : 'icon-select' " class="iconfont"></i>
              开发日志
            </router-link>
          </li>
          <li @click="getIndex(4)">
            <router-link to="/us">
              <i v-bind:class="index === 4? 'icon-people_fill' : 'icon-people' " class="iconfont"></i>
              关于我
            </router-link>
          </li>
        </ul>
      </footer>
    </div>
</template>

<script>
    export default {
        name: "appfooter",
        props:['lodingShow'],
        data(){
          return{
            index:1
          }
        },
      methods:{
          getIndex: function (i) {
            this.index = i;
          }
      }
    }
</script>

<style scoped>

</style>



