import Vue from 'vue'
import Router from 'vue-router'

import Home from '../components/home/Home'
import Index from '../components/index/Index'
import More from '../components/more/More'
import Diary from '../components/diary/Diary'
import Us from '../components/us/Us'
import Music from '../components/more/music/Music'
import MusicList from '../components/more/music/MuicList'
import MusicListParent from '../components/more/music/MusicListParent'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path:'/',
      component:Home,
      redirect:'/index', //默认进入加载的子集
      children:[ //子集路由
        {path:'/index',component:Index},
        {path:'/more',component:More},
        {path:'/diary',component:Diary},
        {path:'/us',component:Us}
      ]
    },
    {
      path:'/musicList/music',
      component: Music
    },
    {
      path:'/musicList',
      component: MusicList
    },
    {
      path:'/MusicListParent',
      component: MusicListParent
    }

  ]
})



