<template>
    <div style="height: 100%;">
      <!--<app-header></app-header>-->
      <loding v-show="isLodingShow" @parentHide="hide()"></loding>
      <app-main></app-main>
      <app-footer :lodingShow="isLodingShow"></app-footer>
    </div>
</template>

<script>
    import appheader from '../header/appheader'
    import appfooter from '../footer/appfooter'
    import appmain from '../main/appmain'
    import loding from '../loding/Loding'
    export default {
        name: "Home",
      //局部组件注册
      data(){
          return{
            isLodingShow:true
          }
      },
      components: {
        'app-header':appheader,
        'app-footer':appfooter,
        'app-main':appmain,
        loding
      },
      methods: {
          hide(){
            this.isLodingShow = false;
          }
      }

    }

</script>

<style scoped>

</style>



