<template>
  <div class="loding-box" @click="hide()">
    <div class="loding-body">
      <div class="loding">
        <span class="X1"></span>
        <span class="X2"></span>
        <span class="X3"></span>
      </div>
      <div class="text-body">
        <p class="text">{{msg}}</p>
        <p class="text-show">{{msg}}</p>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "Loding",
        data(){
          return{
            msg:"我的过去常常在追赶着我。"
          }
        },
        methods:{
          hide(){
            this.$emit('parentHide')
          }
        }
    }
</script>

<style lang="css" scoped>
  @import '../../../static/css/loding.css';
</style>



