<template>
  <div class="music-box">
    <ul class="music-list">
      <!--@click="linkGoTo('/musicList/music',i , music)"-->
      <li v-for="(music,i) in muicListArr" @click="cutMusic(i)">
          <div class="list">
            <p>{{music.musicName}}</p>
            <p class="user-name">{{music.userName}}</p>
          </div>
          <div class="schedule" :style="{width: music.hot + '%'}">热度：{{music.hot}}</div>
      </li>
    </ul>
    <music :index-music="index"></music>
  </div>
</template>



<script>
    import appheader from '../../header/appheader'
    import music from './Music'
    export default {
      name: "MuicList",
      data(){
        return{
          muicListArr:[
            {userName:'周杰伦',musicName:'晴天',hot:95},
            {userName:'周杰伦',musicName:'一路向北',hot:85},
            {userName:'于文文',musicName:'体面',hot:80},
            {userName:'Fitz & the Tantrums',musicName:'HandClap',hot:70}
          ],
          index:-1
        }
      },
      components:{
        'app-header':appheader,
        music,
      },
      methods:{
        linkGoTo (link,index,music) {
          this.$router.push({path:link,query: { userId: index, obj : music}})
        },
        cutMusic(index){
            this.index = index;
         // console.log(this.index);
        }
      },
    }
</script>

<style scoped>

</style>



